import React from 'react';
import { string, shape } from 'react-proptypes';

import './Security.scss';

const SecurityBlock = ({ params }) => (
    <div className="securityBlock__container">
        <div className="securityBlock__descriptionContainer">
            <h2>
                {params.title}
            </h2>

            <p className="securityBlock__description copytext-large">
                {params.description}
            </p>
        </div>

        <div className="securityBlock__text copytext">
            {params.text}
        </div>
    </div>
);

SecurityBlock.propTypes = {
    params: shape({
       title: string,
       description: string,
       text: string,
    })
};

export default SecurityBlock;



