import React from 'react';
import { arrayOf, string, shape } from 'react-proptypes';
import cx from 'classnames';

import { Divider } from '../../../ui';
import './SkillsList.scss';

const Skill = ({ title, icon, color, description }, index) => (
    <div className="skill__container"
        key={index} >
        <i className="material-icons"
            style={{ color }}>
            {icon}
        </i>

        <h3>
            {title}
        </h3>

        <p className="copytext-small">
            {description}
        </p>
    </div>
);

const SkillSet = ({ skills, image, isColumn, reversed }, index) => {
    const cl = cx({
        'skillset__content': true,
        'skillset__content--columns': isColumn,
        'skillset__content--rows': !isColumn,
        'skillset__content--reversed': reversed
    });

    return (
        <div className="skillset__container"
            key={index} >
            <div className={cl}>
                <div className="skillset__image">
                    <img src={`${process.env.ASSETS_URL}${image}`} />
                </div>

                <div className="skillset__items">
                    {skills.map((item, index) => Skill(item, index))}
                </div>
            </div>
        </div>
    );
}

const SkillsList = ({ params }) => {
    const list = [];

    const count = params.length;

    params.forEach((item, index) => {
        const skill = (
            SkillSet(item, index)
        );

        list.push(skill);

        if (index < count - 1) {
            list.push(<Divider key={index + 'D'} />);
        }
    });

    return (
        <div className="skillsList__container">
            {list}
        </div>
    );
};

SkillsList.propTypes = {
    items: arrayOf(shape({
        title: string,
        description: string,
        action: string,
        actionUrl: string,
        image: string
    }))
};

SkillsList.defaultProps = {
    items: []
};

export default SkillsList;



